
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListSubheader from '@material-ui/core/ListSubheader';
import Avatar from '@material-ui/core/Avatar';
import Link from '@material-ui/core/Link';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import Divider from '@material-ui/core/Divider';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CheckIcon from '@material-ui/icons/Check';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    backgroundColor: '#ffffff',

  },

  listHeader:{
    textAlign: 'center',
    fontSize: '1.8rem',
    color: '#000000',
    fontWeight: 300,
    fontFamily: 'Inter, sans-serif',
    padding: theme.spacing(2, 0, 1),

  },
  headerHero:{
    fontWeight: 800,
    fontSize:'6.8rem',
    fontFamily: 'IBM Plex Sans, sans-serif',
    color:'#000',
  },
  subHeaderHero:{
    fontWeight: 100,
    fontSize:'4.1rem',
    fontFamily: 'Lobster, cursive',
    color:'#000',
  },
  container:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(8, 0, 4),
  },
  paperFrame:{
    border: '0px solid',
    boxShadow:'0 8px 30px rgba(0,0,0,0.18)',
    borderRadius:'5px',
    p:0,
    lineHeight:0,
  },
}));

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

const HeroSection = () => {
  const classes = useStyles();

  return(
    <Box className={classes.root}>
      <Container maxWidth="md" >
        <Container className={classes.container}>
          <Typography
            align="center"
            color="text.primary"
            variant="h1"
             className={classes.headerHero}
gutterBottom
          >
            <strong>JustShapes</strong>
          </Typography>
          <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
            The human brain likes shapes, patterns, and fluid movements.<br />
            JustShapes are NFTs minted on the Solana blockchain.<br />
            Read below to learn what they bring the community.
          </Typography>
          <Container maxWidth="xs" style={{paddingTop:'10px'}}>
          <Grid container columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
            <Grid item xs={4}>
              <Typography variant="h5" align="center" color="textPrimary" >
                <strong>5000</strong>
              </Typography>
              <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
                SHAPES
              </Typography>
            </Grid>
            <Grid item xs={4}>
              <Typography variant="h5" align="center" color="textPrimary" >
                <strong>60</strong>
              </Typography>
              <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
                FPS
              </Typography>
            </Grid>
            <Grid item xs={4}>
              <Typography variant="h5" align="center" color="textPrimary" >
                <strong>10 SEC</strong>
              </Typography>
              <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
                LOOPS
              </Typography>
            </Grid>

          </Grid>

          </Container>
        </Container>

        </Container>
    </Box>
  );
}

export default HeroSection;
