import { makeStyles } from '@material-ui/core/styles';
import React, { useState, useCallback } from "react";
import ReactDOM from "react-dom";
import ReactPlayer from 'react-player'
import Chip from '@material-ui/core/Chip';
//import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import video726MP4 from "../assets/videos/726.mp4";
import Badge from '@material-ui/core/Badge';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
    color: '#000000 !important',
    fontWeight: '700 !important',
    border:'0px solid red',
  },
  dividerStyle:{
    margin: theme.spacing(4),
  },
  container:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(10, 0, 10),
  },
  containerSection:{
    backgroundColor: '#ffffff !important',
    padding: theme.spacing(15, 0, 5),
  },
  button:{
    width:'100%',
    justifyContent: 'center',
    padding: theme.spacing(2, 0, 2),

  },
  paperFrame:{
    border: '0px solid',
    boxShadow:'0 8px 30px rgba(0,0,0,0.32)',
    borderRadius:'5px',
    p:0,
    lineHeight:0,
    overflow:'hidden',
  },
  artDiv:{
    border:'0px solid red',

  },
  headerHero:{
    fontWeight: 800,

    fontFamily: 'IBM Plex Sans, sans-serif'
  },
  chipRed:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#EA3323',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipBlue:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#007CF3',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipGreen:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#1FB254',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipYellow:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#FFBB25',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipPurple:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#9c78ff',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipOrange:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#ff8c01',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipWhite:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#ffffff',
    color:'#ffffff',
    border:'1px solid #333',
  },
  chipBlack:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#000000',
    color:'#ffffff',
    border:'1px solid #333',
  },
}));

const ShapesShowcase = () => {
  const classes = useStyles();
  const [url, setUrl] = useState(video726MP4);
  const circle = <div className={clsx(classes.shape, classes.shapeCircle)} />;

  return(
    <Box>
    <Container className={classes.containerSection}>

      <Container maxWidth="lg">
        <Grid container spacing={4} direction="row" alignItems="center" className={classes.artDiv}>
          <Grid item xs={12} sm={12} md={6} className={classes.artDiv}>
          <div className={classes.artDiv} alignItems="center">
            <Paper className={classes.paperFrame} >
              <div className="boxybox">
                <ReactPlayer width='100%' height='100%' playing loop muted={true} controls={false} playsinline={true} url={url} type="video/mp4" />
              </div>
            </Paper>
          </div>
          </ Grid>
          <Grid item xs={12} sm={12} md={6} order={{ xs: 2, sm: 2 }} className={classes.artDiv}>
            <Container maxWidth="xs">
            <Typography component="h1" variant="h5" align="center" color="textPrimary" gutterBottom>
              <strong>4 Sizes Classes </strong>
            </Typography>

            <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
            Single: 1 Shape <Chip className={classes.chipCommon} label="Exotic" size="small" /><br />
            Mini: 4-16 Shapes <Chip className={classes.chipCommon} label="Rare" size="small" /><br />
            Regular: 36-64 Shapes <Chip className={classes.chipCommon} label="Common" size="small" /><br />
            Mega: 400-900 Shapes <Chip className={classes.chipCommon} label="Common" size="small" />
            </Typography>

              <Divider className={classes.dividerStyle} variant="middle" />
              <Typography component="h1" variant="h5" align="center" color="textPrimary" gutterBottom>
                <strong>Shape #726</strong>
              </Typography>
              <Typography variant="h6" align="center" color="textSecondary" gutterBottom>
                Type: Wireframe<br />
                Shape: Cuboid<br />
                Pattern: Random<br />
                Size: Regular (8x8)<br />
                Colors:&nbsp;
                <Chip className={classes.chipRed} label="" />
                <Chip className={classes.chipBlue} label="" />
                <Chip className={classes.chipWhite} label="" />
              </Typography>
              </Container>
            </ Grid>

          </ Grid>
      </ Container>
      </ Container>
    </Box>
  );
}

export default ShapesShowcase;
